@-webkit-keyframes EnergySpin {
    from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes EnergySpin {
    from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.HighLighter > h1 {
    text-underline: none;
    top: 60%;
    -ms-flex-align: center;
        align-items: center;
    display: -ms-flexbox;
    display: flex;
}


